import { DataUtil } from '@youfoundation/dotyoucore-transit-lib';
import { MutableRefObject, useRef } from 'react';
import { createAuthenticationProvider } from '../../provider/AuthenticationProvider';

const SHARED_SECRET = 'HSS';

const useAuth = () => {
  const authenticate = (identity: string, returnUrl: string): void => {
    const strippedIdentity = identity.replace(new RegExp('^(http|https)://'), '').split('/')[0];

    const client = createAuthenticationProvider();
    client.authenticate(strippedIdentity, returnUrl);
  };

  const finalizeAuthentication = (
    rsaEncryptedSharedSecret64: string | null,
    returnUrl: string | null
  ) => {
    if (!rsaEncryptedSharedSecret64 || !returnUrl) {
      return;
    }

    //TODO RSA decrypt
    const ss = rsaEncryptedSharedSecret64;
    window.localStorage.setItem(SHARED_SECRET, ss);
    window.location.href = returnUrl;
  };

  const logout = async (): Promise<void> => {
    const client = createAuthenticationProvider();
    await client.logout();
    authenticationState.current = 'anonymous';
  };

  const authenticationState: MutableRefObject<'unknown' | 'anonymous' | 'authenticated'> =
    useRef('unknown');

  const initialize = async () => {
    const client = createAuthenticationProvider();
    // If there is no shared secret, then it's not even worth it to check on server side if the cookie would suffice..
    const hasValidToken = getSharedSecret() ? await client.hasValidToken() : false;

    authenticationState.current = hasValidToken ? 'authenticated' : 'anonymous';
  };

  const getSharedSecret = () => {
    const raw = window.localStorage.getItem(SHARED_SECRET);
    if (raw) {
      return DataUtil.base64ToUint8Array(raw);
    }
  };

  (async () => {
    if (authenticationState.current === 'unknown') {
      await initialize();
    }
  })();

  return {
    authenticate,
    finalizeAuthentication,
    logout,
    getSharedSecret,
    isAuthenticated: authenticationState.current === 'authenticated',
  };
};

export default useAuth;
